import React from 'react';
import { Media } from 'react-bootstrap';
import AppMarkdown from '../basic/app-markdown';
import { appModule } from '../../utilities/enums';

function OrganizationTimelineWidget(props) {
    return (
        <>
            <Media>
                <img
                    width={64}
                    height={64}
                    className="mr-3"
                    src={require('../../assets/images/' + props.logoFile)}
                    alt={props.name}
                />
                <Media.Body>
                    <h6>{props.keyAreas[0]}</h6>
                    <AppMarkdown
                        fileName={props.descriptionFile}
                        moduleType={appModule.Organization}></AppMarkdown>
                </Media.Body>
            </Media>
        </>
    );
}

export default OrganizationTimelineWidget;