import React from 'react';
import { Row, Col } from 'react-bootstrap';
import * as ExperienceApi from '../../data/experience';
import { appModule } from '../../utilities/enums';
import { Timeline, FilterList, SelectMenu, Button } from '@primer/components';
import AppTimelineCard from '../basic/app-timeline.card';
import { useState } from 'react';
import { FiGitCommit } from 'react-icons/fi';

function OrganizationTimeline(props) {
    const totalExperience = ExperienceApi.getTotalExperience(appModule.Organization);

    let timelineInfo = props.timeline ? props.timeline : ExperienceApi.getOrganizationTimeline();
    const [timeline, setTimeline] = useState(timelineInfo);
    const [filterYear, setFilterYear] = useState(Number);
    const showYearFilter = props.showYearFilter != undefined ? props.showYearFilter : true;

    const getExperienceYear = function () {
        let years = [];
        for (let i = totalExperience.toYear; i >= totalExperience.fromYear; i--) {
            years.push(i);
        }
        return years;
    }

    const setFilteredYear = function (year) {
        setFilterYear(year);
        setTimeline(ExperienceApi.filterExperienceByYear(appModule.Organization, year));
    }

    const clearFilter = function () {
        setFilterYear(null);
        setTimeline(ExperienceApi.getOrganizationTimeline());
    }

    return (
        <Row>
            <Col md={showYearFilter ? 10 : 12}>
                <Timeline clipSidebar={true} className="mar-top-md">
                    {
                        timeline.map((item, idx) => {
                            return (
                                <AppTimelineCard {...item} key={idx}></AppTimelineCard>
                            );
                        })
                    }
                    <Timeline.Item condensed>
                        <Timeline.Badge>
                            <FiGitCommit />
                        </Timeline.Badge>
                        <Timeline.Body>Total Professional Experience: <b>{totalExperience.totalExperience}</b></Timeline.Body>
                    </Timeline.Item>
                </Timeline>
            </Col>
            {showYearFilter ?
                <Col md={2}>
                    <FilterList className="mar-top-lg">
                        {
                            (getExperienceYear()).map((val, idx) => {
                                return (
                                    <FilterList.Item key={idx} selected={filterYear == val} onClick={() => setFilteredYear(val)}>
                                        <span style={filterYear == val ? { color: 'white' } : {}}>{val}</span></FilterList.Item>
                                );
                            })
                        }
                        <FilterList.Item href="#" onClick={() => clearFilter()}>Clear</FilterList.Item>
                    </FilterList>
                </Col> : null}
        </Row>
    );
}

export default OrganizationTimeline;