import React from 'react';
import { Row, Col, Card, Container, ListGroup } from 'react-bootstrap';
import RepositoryCard from './repository-card';
import { RepositoryApi } from '../../data/app-data';
import AppModal from '../basic/app-modal';
import { useState } from 'react';

function RepositoryPage() {
    let repositories = RepositoryApi.getAll();
    const [modalShow, setModalShow] = React.useState(false);
    const [modalProps, setModalProps] = useState({});

    function showRepositoryDetails(rep) {
        console.log(rep);
        setModalProps({
            "title": rep.title,
            "showMarkdown": true,
            "fileName": rep.descriptionFile
        });
        setModalShow(true);
    }

    return (
        <>
            <AppModal {...modalProps} show={modalShow}
                onHide={() => setModalShow(false)}></AppModal>
            <Container className="mar-top-md mar-bottom-md">
                <ListGroup variant="flush" className="rep-card">
                    {repositories.map((x, i) => {
                        return (
                            <RepositoryCard props={x} key={i} onHeaderClicked={showRepositoryDetails} />
                        );
                    })}
                </ListGroup>
            </Container>
        </>
    );
}

export default RepositoryPage;