import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';
import { getKeyByEnumValue, appModule } from '../../utilities/enums';
// import ContentUrl from '../../content/organizations/cbre-project.md';

function AppMarkdown(props) {
    let { moduleType, fileName } = props;
    const [mdText, setMdText] = useState('');
    var fileUrl = "";
    let moduleName = getKeyByEnumValue(appModule, moduleType);
    console.log('../../content/' + moduleName + '/' + fileName);
    try {
        fileUrl = require('../../content/' + moduleName + '/' + fileName);
    }
    catch{
    }

    useEffect(() => {
        if (fileUrl != "") {
            fetch(fileUrl)
                .then((response) => {
                    if (response.ok) return response.text();
                    else return Promise.reject("Didn't fetch text correctly");
                })
                .then((text) => {
                    setMdText(text);
                })
                .catch((error) => console.error(error));
        }
    });

    return (
        <ReactMarkdown source={mdText}></ReactMarkdown>
    );
}

export default AppMarkdown;