import React from "react";
import { Timeline, Details, Button, Link } from '@primer/components';
import { GoMilestone, GoSquirrel } from 'react-icons/go';
import { BsArrowsExpand, BsArrowsCollapse } from 'react-icons/bs';
import { appModule } from "../../utilities/enums";
import { covertToDate } from "../../utilities/extensions";
import OrganizationTimelineWidget from "../organization/organization-timeline.widget";

function AppTimelineCard(props) {
    return (
        <>
            <Timeline.Item>
                <Timeline.Badge bg={props.isWorking ? "green.5" : "#e1e4e8"}>
                    {props.type == appModule.Organization ? (props.isWorking ? <GoSquirrel color={"white"} /> : <GoMilestone />) : null}
                </Timeline.Badge>
                <Timeline.Body>
                    <div>
                        <Link href="#" fontWeight="bold" color="gray.8" mr={1} muted>
                            {props.organization.name}
                        </Link>
                        {props.isWorking ? "working from" : "joined on"} {covertToDate(props.fromDate)}.
                        &nbsp;
                        Exp:&nbsp;
                    <Link href="#" color="gray.7" muted>
                            {props.experience}
                        </Link>
                    </div>
                    <small><i>Expend to see more info.</i></small>
                    <Details>
                        {({ open }) => (
                            <>
                                <Link as="summary" className="timeline-expend-icon">
                                    {open ? <BsArrowsCollapse /> : <BsArrowsExpand />}
                                </Link>
                                <OrganizationTimelineWidget {...props.organization} />
                            </>
                        )}
                    </Details>
                </Timeline.Body>
            </Timeline.Item>
        </>
    );
}

export default AppTimelineCard;